body {
  background-color: #1a1a1a;
  color: #ffffff;
}

.app {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #333;
}

.app-header > div {
  display: flex;
  align-items: center;
  gap: 20px;
}

.connection-status {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background-color: #2d2d2d;
  border-radius: 8px;
  font-size: 14px;
}

.dark-theme .connection-status {
  background-color: #2d2d2d;
  color: #fff;
}

.light-theme .connection-status {
  background-color: #f5f5f5;
  color: #000;
}

.status-indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 8px;
}

.connected {
  background-color: #4CAF50;
}

.disconnected {
  background-color: #F44336;
}

.app-content {
  display: flex;
  gap: 20px;
}

.sidebar {
  width: 300px;
  padding: 15px;
  background-color: #2d2d2d;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.3);
}

.main-content {
  flex: 1;
  overflow-x: auto;
  background-color: #2d2d2d;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.3);
  padding: 15px;
}

.orderbook-table-container {
  overflow-x: auto;
  min-width: 100%;
}

.orderbook-table {
  width: 100%;
  border-collapse: collapse;
}

.orderbook-table th, 
.orderbook-table td {
  border: 1px solid #444;
  padding: 12px;
  text-align: center;
}

.orderbook-table th {
  background-color: #1a1a1a;
  font-weight: bold;
}

.token-cell {
  font-weight: bold;
  background-color: #1a1a1a;
}

.type-cell {
  font-weight: bold;
  width: 60px;
}

.price-cell {
  cursor: pointer;
}

.price-cell:hover {
  background-color: #3d3d3d;
}

.sell-price {
  color: #F44336;
}

.buy-price {
  color: #4CAF50;
}

.highlight-green {
  background-color: rgba(76, 175, 80, 0.15);
}

.highlight-red {
  background-color: rgba(244, 67, 54, 0.15);
}

.actions {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

button {
  padding: 8px 12px;
  border: none;
  border-radius: 4px;
  background-color: #2196F3;
  color: white;
  cursor: pointer;
  font-size: 14px;
}

button:hover {
  background-color: #0b7dda;
}

.update-button {
  background-color: #4CAF50;
}

.update-button:hover {
  background-color: #3e8e41;
}

.clear-button {
  background-color: #F44336;
}

.clear-button:hover {
  background-color: #d32f2f;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: #2d2d2d;
  border-radius: 8px;
  padding: 20px;
  width: 80%;
  max-width: 800px;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 5px 15px rgba(0,0,0,0.5);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.modal-close {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #999;
}

.modal-close:hover {
  color: #fff;
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

input, select {
  width: 100%;
  padding: 8px;
  border: 1px solid #444;
  border-radius: 4px;
  box-sizing: border-box;
  background-color: #1a1a1a;
  color: #ffffff;
}

.settings-slider {
  display: flex;
  align-items: center;
  gap: 10px;
}

.settings-slider label {
  min-width: 180px;
}

.settings-slider input[type="range"] {
  flex: 1;
}

.settings-slider .value {
  min-width: 50px;
  text-align: right;
}

.token-list, .exchange-list {
  max-height: 200px;
  overflow-y: auto;
  border: 1px solid #444;
  border-radius: 4px;
  margin-bottom: 10px;
  background-color: #1a1a1a;
}

.list-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 12px;
  border-bottom: 1px solid #444;
}

.list-item:last-child {
  border-bottom: none;
}

.list-item .remove-button {
  background-color: #F44336;
  padding: 4px 8px;
  font-size: 12px;
}

.order-detail-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.order-detail-table th, 
.order-detail-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: right;
}

.order-detail-table th {
  background-color: #f2f2f2;
  font-weight: bold;
}

.theme-toggle {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-left: 16px;
}

.theme-toggle button {
  padding: 6px 12px;
  background-color: #333;
  color: #fff;
  border: 1px solid #555;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.theme-toggle button:hover {
  background-color: #444;
}

.theme-toggle button.active {
  background-color: #2196F3;
  border-color: #2196F3;
}

/* Dark theme (default) */
.dark-theme {
  background-color: #1a1a1a;
  color: #ffffff;
}

/* Light theme */
.light-theme {
  background-color: #ffffff;
  color: #000000;
}

.light-theme .app-header {
  border-bottom: 1px solid #eee;
}

.light-theme .sidebar,
.light-theme .main-content {
  background-color: #ffffff;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.light-theme .orderbook-table th, 
.light-theme .orderbook-table td {
  border: 1px solid #ddd;
}

.light-theme .orderbook-table th {
  background-color: #f2f2f2;
}

.light-theme .token-cell {
  background-color: #f9f9f9;
}

.light-theme .price-cell:hover {
  background-color: #f0f0f0;
}

.light-theme .modal-content {
  background-color: #ffffff;
}

.light-theme input,
.light-theme select {
  background-color: #ffffff;
  color: #000000;
  border: 1px solid #ddd;
}

.light-theme .token-list,
.light-theme .exchange-list {
  background-color: #ffffff;
  border: 1px solid #ddd;
}

.light-theme .list-item {
  border-bottom: 1px solid #eee;
}

.light-theme .highlight-green {
  background-color: rgba(76, 175, 80, 0.2);
}

.light-theme .highlight-red {
  background-color: rgba(244, 67, 54, 0.2);
}

.status-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
  background-color: #2d2d2d;
  padding: 12px;
  border-radius: 8px;
}

.light-theme .status-group {
  background-color: #f5f5f5;
}

.status-legend {
  display: flex;
  gap: 16px;
  font-size: 12px;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 6px;
}

.status-indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  flex-shrink: 0;
}

.connected, .has-data {
  background-color: #4CAF50;
}

.no-data {
  background-color: #FFC107;
}

.disconnected {
  background-color: #F44336;
}